import React, { useRef, useEffect, Suspense } from 'react';
import { useThree, useFrame } from '@react-three/fiber';
import { useTheme, useMediaQuery } from "@material-ui/core";
import {calculateScrollOffset, lerp} from "../CustomMath";
import Model3D from "./Model3D";
import { Model } from "./Archangel Michael Statue";

const ArchangelMichaelStatue3D = ({ path, material, position, rotation, scale, scrollRotationFactor }) => {
    const modelRef = useRef();
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
    //const isTablet = useMediaQuery(theme.breakpoints.between('sm', 'md'));
    let scrollOffset = 0;

    const { gl } = useThree();
    const canvasRef = useRef();

    useEffect(() => {
        canvasRef.current = gl.domElement;

        if (canvasRef.current) {
            // Set a CSS transition for opacity so changes animate over 1 second
            canvasRef.current.style.transition = "opacity 1s ease-in-out";
        }
    }, [gl]);

    useFrame(() => {
        if (modelRef.current && canvasRef.current) {
            scrollOffset = calculateScrollOffset(canvasRef.current.offsetTop, -550);

            let limitedScrollOffset = (scrollOffset * 2 > 1) ? 1 : scrollOffset * 2;
            
            // Calculate new position, rotation and scale values

            let xPositionValue = 0;
            let yPositionValue = 0;
            let yRotationValue = 0;
            let scaleValue = scale;

            if(isMobile) {
                xPositionValue = position[0];
                yPositionValue = position[1] - limitedScrollOffset * 8;

                yRotationValue = limitedScrollOffset * scrollRotationFactor;
            } else {
                xPositionValue = position[0] + limitedScrollOffset * 2;
                yPositionValue = position[1] - limitedScrollOffset * 15.25;

                yRotationValue = limitedScrollOffset * scrollRotationFactor;
            }
            scaleValue = (1 + limitedScrollOffset) * scale;

            // Apply scroll animation

            modelRef.current.position.x = lerp(modelRef.current.position.x, xPositionValue);
            modelRef.current.position.y = lerp(modelRef.current.position.y, yPositionValue);

            const initialYRotation = rotation[1] * (Math.PI / 180);
            modelRef.current.rotation.y == rotation[1] ? modelRef.current.rotation.y = initialYRotation + yRotationValue : modelRef.current.rotation.y = lerp(modelRef.current.rotation.y, initialYRotation + yRotationValue);

            const smoothedScaleValue = lerp(modelRef.current.scale.x, scaleValue);
            modelRef.current.scale.set(smoothedScaleValue, smoothedScaleValue, smoothedScaleValue);

            // Fade the canvas opacity

            //canvasRef.current.style.opacity = scrollOffset >= (isMobile? 0.75 : 1) ? 0 : 1;
            canvasRef.current.style.opacity = scrollOffset >= (isMobile? 0.95 : 1) ? 0 : 1;
        }
    });

    return (
        <Suspense fallback={ null }>
            {/*
            <Model3D 
                ref={modelRef} 
                path={path} 
                material={material} 
                position={position} 
                rotation={rotation} 
                scale={scale} 
                canRotate={false} 
            />
            <mesh ref={modelRef} position={position}>
                <boxGeometry args={[10, 10, 10]} />
                <meshStandardMaterial color="orange" />
            </mesh>
            */}
            <Model 
                ref={modelRef}
                material={material}
                position={position} 
                rotation={rotation}
            />
        </Suspense>
    )
};

export default ArchangelMichaelStatue3D;